<template>
  <div class="text-small border p-3">
    <p class="align-self-start">売上: {{ sales }}円</p>
    <ul class="mt-1">
      <li v-for="coin in stock" :key="`stock_${coin.value}`" :class="stockClass(coin.value)"><span>{{ coin.value }}円</span>: {{ coin.count }}枚</li>
    </ul>
  </div>
</template>

<script>

import { Component, Vue, Prop } from 'vue-property-decorator';

export default @Component() class VendingMachineStock extends Vue {
  @Prop() stock;
  @Prop() sales;


  stockClass(val) { 
    const defaultClass = "mr-4"
    const valueStock = this.stock.find((s) => s.value === val)
    return `${defaultClass} ${ valueStock && valueStock.count === 0 ? "text-danger" : "" }`
  }
}
</script>

<style scoped>

.text-small {
  font-size: 0.8rem;
}

span {
  display: inline-block;
  width: 65px;
}

.--shortage {
  color: red;
}

ul {
  list-style: none;
  display: inline-grid;
  grid-template: 1fr 1fr / 1fr 1fr 1fr
}

ul li {
  display: inline-block;
}
</style>
