<template>
  <div id="app" class="d-flex flex-row p-4">
    <VendingMachine
      :products="displayProducts"
      :sales="sales"
      :stock="changeStock"
      :inputs="inputs"
      @eject="onEject"
      @purchased="onPurchased" />
    <UserStatus class="ml-2" :message="message" :products="products" :inputs="inputs" @changeInputs="onChangeValue" @purchased="onPurchased" />
  </div>
</template>

<script>
import VendingMachine from './components/VendingMachine.vue'
import UserStatus from './components/UserStatus.vue'
import InitialProducts from './data/products'
import * as calculator from './utils/calculator'
import * as Coin from './data/coin'

import { Component, Vue } from 'vue-property-decorator';

export default @Component({
  components: {
    VendingMachine,
    UserStatus
  }
}) class App extends Vue {
  message = { type: "normal", text: "自動販売機がある" }
  inputs = Coin.createInitialInputs();
  changeStock = [
    {
      value: 10,
      count: 10,
    },
    {
      value: 50,
      count: 10,
    },
    {
      value: 100,
      count: 10,
    },
    {
      value: 500,
      count: 10,
    },
    {
      value: 1000,
      count: 5
    }
  ]
  products = InitialProducts;
  sales = 0;
  change = null;

  get displayProducts() {
    return this.products.map((product) => {
      const change = this.sum - product.price
      const nextChangeStock = this.changeStock.map((stock) => {
        const input = this.inputs.find((i) => i.value === stock.value )
        return {  value: stock.value, count: input.count + stock.count }
      })

      const { error } = calculator.getChange(change, nextChangeStock)
      const hasChangeStock = error === false
      return {
        ...product,
        available: hasChangeStock && this.sum >= product.price
      }
    })
  }

  get sum () {
    return calculator.getSum(this.inputs)
  }

  onChangeValue (value) {
    this.message = { type: "normal", text: "" };
    // 指定の効果と札以外は, 受け付けない
    if (!Coin.isAvailable(value)) {
        this.message = { type: "error", text: "そのお金は使えないようだ" }
        return;
    }
    const coinStock = this.inputs.find((input) => input.value === value);
    coinStock.count += 1;
    const label = Coin.getLabel(value);
    this.message = { type: "normal", text: `${label} を入れた。合計${this.sum}円` }
  }

  onEject () {
    this.change = calculator.getSum(this.inputs);
    this.inputs = Coin.createInitialInputs();
    this.message = { type: "normal", text: `${this.change}円払い出した` }
  }

  onPurchased (product) {
    if (product.stock === 0) {
      this.message = {
        type: "error",
        text: `${product.name}は売り切れている`
      }
      return;
    }

    if (product.price > this.sum) {
      this.message = {
        type: "error",
        text: "お金が足らない！"
      }
      return;
    }
    const change = this.sum - product.price

    const nextChangeStock = this.changeStock.map((stock) => {
      const input = this.inputs.find((i) => i.value === stock.value )
      return {  value: stock.value, count: input.count + stock.count }
    })

    const { result, error } = calculator.getChange(change, nextChangeStock)

    if (error) {
      this.message = {
        type: "error",
        text: "釣り銭不足のため購入できなかった"
      }
      return;
    }

    this.change = calculator.getSum(result)
    this.inputs = Coin.createInitialInputs();
    this.changeStock = nextChangeStock.map((stock) => {
      const ejectedStock = result.find((i) => i.value === stock.value )
      return { value: stock.value, count: stock.count - ejectedStock.count }
    })

    console.info(this.changeStock);
    product.stock -= 1
    this.sales += product.price
    this.message = {
      type: "normal",
      text: `${product.name}を手に入れた! お釣り:${this.change}円`
    }
  }
}
</script>

