<template>
  <div class="border border-secondary d-flex flex-column p-4 mt-2 w-75">
    <h2 class="border boder-secondary p-2 text-center w-50 align-self-center">自動販売機</h2>
    <Products @purchased="onPurchased" :products="products" class="m-4" />
    <div class="d-flex justify-content-between mx-4">
      <VendingMachineStock :sales="sales" :stock="stock" />
      <div class="d-flex flex-column">
        <CashDisplay :inputs="inputs"/>
        <button class="btn-primary align-self-end p-2 rounded" @click="onClickEject">払い出し</button>
      </div>
    </div>
    <div class="outlet bg-dark align-self-center m-4 w-50"></div>
  </div>
</template>

<script>
import CashDisplay from './CashDisplay.vue'
import Products from './Products.vue'
import Form from './Form.vue'
import VendingMachineStock from './VendingMachineStock.vue'

import { Component, Vue, Prop, Emit } from 'vue-property-decorator';

export default @Component({
  components: {
    CashDisplay,
    Products,
    Form,
    VendingMachineStock
  }
}) class VendingMachine extends Vue {
  @Prop() inputs;
  @Prop() sales;
  @Prop() products;
  @Prop() stock;

  @Emit("input")
  onChangeValue (key) {
    return key;
  }

  @Emit("eject")
  onClickEject () {
    return this.inputs;
  }

  @Emit("purchased")
  onPurchased (product) {
    return product
  }
}
</script>

<style scoped>

.outlet {
  height: 40px;
  opacity: 0.5;
}
</style>
