<template>
  <div class="form-root d-flex flex-column justify-content-center align-items-end mt-2" @click="showPriceMenu = false">
    <div class="menu">
      <h6 class="title">どうする?</h6>
      <ul>
        <li @click.stop="showPriceMenu = !showPriceMenu; showProductsMenu = false" class="position-relative">お金を入れる
          <div class="menu stage-2" v-if="showPriceMenu">
            <h6 class="title">どれを？</h6>
            <ul>
              <li v-for="coin in coins" :key="coin" @click.stop="add(coin)">{{ coin }}円</li> 
            </ul>
          </div>
        </li>
        <li class="position-relative" @click.stop="showProductsMenu = !showProductsMenu; showPriceMenu = false">買う
            <div class="menu stage-2" v-if="showProductsMenu">
            <h6 class="title">どれを？</h6>
            <ul>
              <li v-for="product in products" :key="product.name" @click.stop="purchase(product)">{{ product.name }}</li>
            </ul>
          </div>
        </li>
        <li class="disabled">飲む</li>
      </ul>
    </div>
  </div>
</template>

<script>

import { Component, Vue, Prop } from 'vue-property-decorator';
import { COINS } from "../data/coin"

export default @Component({}) class Form extends Vue {
  @Prop() value;
  @Prop() products;

  showPriceMenu = false;
  showProductsMenu = false;


  get coins() {
    return COINS
  }


  add(coin) {
    this.$emit("input", coin);
  }

  purchase(product) {
    this.$emit("purchased", product);
  }
}
</script>

