<template>
    <div class="d-flex flex-column justify-content-center">
      <Form :value="inputs" :products="products" @input="onChangeValue" @purchased="purchased" />
      <div class="h-25 align-self-end">
        <img class="h-100" src="../assets/drink_nodo_kawaita_man.png">
      </div>
      <div class="message-logs p-2 align-self-end w-100">
        <p v-if="logTextVisible" :class="messageClass"> {{ message.text }}</p>
      </div>
    </div>
</template>

<script>
import Form from './Form.vue'

import { Component, Vue, Prop, Watch } from 'vue-property-decorator';

export default @Component({
  components: {
    Form,
  }
}) class UserStatus extends Vue {
  @Prop() inputs;
  @Prop() message;
  @Prop() products;

  logTextVisible = true;
  changeTimer = null;

  onChangeValue (val) {
    this.$emit("changeInputs", val);
  }

  purchased (product) {
    this.$emit("purchased", product);
  }

  @Watch("message.text")
  onChangeMessage() {
    clearTimeout(this.changeTimer);
    this.changeTimer = this.logTextVisible = false;
    window.setTimeout(() => {
      clearTimeout(this.changeTimer);
      this.logTextVisible = true;
    }, 1)
  }

  get messageClass() {
    const defaultClass = "message-log";
    if (this.message.type == "error") {
      return `${defaultClass} text-danger`
    } else {
      return `${defaultClass}`
    }
  }
}
</script>

<style scoped>

.message-logs {
  border: 3px solid black;
  position: relative;
  border-radius: 12px;
  height: 100px;
  font-size: 1rem;
  background-color: #fff;
}

.message-logs p {
  width: 96%;
  margin: 2%;
  white-space: pre;
  overflow: hidden;
  -webkit-animation: ozDq 1s steps(70, end);
}

.message-logs p:after {
  -webkit-animation: ozPulse 1s infinite ease;
}

@-webkit-keyframes ozDq { from { width: 0; } } /* 開始地点の指定 */
@-moz-keyframes ozDq { from { width: 0 } }
@-webkit-keyframes ozPulse { { /* 透明度の指定 */
  from { opacity: 1; }
  to { opacity: 0; }
}
@-moz-keyframes ozPulse {
from { opacity: 1; }
to { opacity: 0; }
}
}

</style>
