<template>
  <div class="d-flex flex-wrap w-80 justify-content-center border">
    <div v-for="product in products" :key="product.name" class="position-relative">
      <div v-if="product.stock === 0" class="position-absolute h-100 w-100 text-danger" @click="purchased(product)">
        <b-icon-X width="100%" height="100%"/>
      </div>
      <div v-if="product.available" class="avaulable position-absolute h-100 w-100 border border-info" @click="purchased(product)">
      </div>
      <div :class="`d-flex flex-column justify-content-center text-center product ${ product.stock <= 0 && 'disable' }`" role="button" @click="purchased(product)">
        <h1><b-icon-trash2-fill/></h1>
        <div class="info">
          <p class="mb-0">{{ product.name }}</p>
          <p class="mb-0">{{ product.price }}円</p>
          <p class="mb-0">残: {{ product.stock }}個</p>
        </div>
      </div>
    </div>
  </div> 
</template>

<script>
import { Component, Vue, Prop } from 'vue-property-decorator';

export default @Component({}) class Products extends Vue {
  @Prop() products;

  purchased(product) {
    this.$emit("purchased", product)
  }

}
</script>

<style scoped>

.product {
  width: 120px;
  height: 150px;
}

.product:hover {
  border: 1px solid black;
}

.product.disable {
  cursor: not-allowed;
}

.avaulable {
  cursor: pointer;
}

.info {
  font-size: 0.8rem;
}
</style>

