
import * as Coin from "../data/coin";

export const getSum = (inputs) => {
  return inputs.reduce((prev, current) => prev + current.value * current.count, 0)
}

const nextCheckValue = (currentValue) => {
  const sorted = Coin.AVAILABLE_COINS.sort((a, b) => b - a);
  const index = sorted.indexOf(currentValue);
  return sorted[index + 1] || null
}

export const getLabel = (key) => {
  switch (key) {
    case "thousand":
    return "千円札";
    case "fiveHundreds":
    return "500円玉";
    case "hundreds":
    return "100円玉";
    case "fifty":
    return "50円玉";
    case "ten":
    return "10円玉";
  }
}


export const getChange = (changeSum, stock) => {

  const currentChange = Coin.AVAILABLE_COINS.map((val) => {
    return { value: val, count: 0}
  });
    
  const checkChangeSum = (currentCheckValue, current) => {
    const currentSum = getSum(current);
    const restSum = changeSum - currentSum;
    const changeValue = Math.floor(restSum / currentCheckValue);
    const currentCheckCoin = current.find((c) => c.value === currentCheckValue)
    const currentValueStock = stock.find((s) => s.value === currentCheckValue)
    currentCheckCoin.count = changeValue;
    const nextSum = getSum(current);
    if (nextSum === changeSum && changeValue <= currentValueStock.count) {
      return { matched: true,  change: current };
    } else if (currentCheckValue === 10) {
      return { matched: false, change: null };
    }

    currentCheckCoin.count = Math.min(changeValue, currentValueStock.count);
    return checkChangeSum(nextCheckValue(currentCheckValue), current);
  }
  
  const { matched, change } = checkChangeSum(1000, currentChange);

  console.info(matched, change)
  return {
    result: change,
    error: !matched
  }
}
